.footer {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 25px 30px 35px;
	color: #ccc;
	background-color: #333;

	&__container {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 30%;
	}

	&__title {
    position: relative;
    margin: 0;
    margin-bottom: rem(10);
    color: #ddd;
    font-size: rem(21);
    font-weight: 400;
    line-height: rem(30);
    text-align: center;

    &::after {
      position: absolute;
      left: calc(50% - 21px);
      content: '';
      display: block;
      width: rem(43);
      height: 2px;
      background-color: #ddd;
    }
  }

  &__text {
  	margin: 0;
  	text-align: center;
  	font-size: 16px;
  	line-height: 18px;

  	&--big {
  		margin-bottom: 25px;
  	}

  	&--contact {
  		margin-bottom: 25px;
  		padding: 0 50px;
  		font-size: 14px;
  	}
  }

  &__list {
  	display: flex;
  	flex-direction: column;
  	justify-content: space-between;
  }

  &__item {
  	display: flex;
  	flex-direction: row;
  	justify-content: space-between;
  	align-items: center;
  	margin-bottom: 10px;
  }

  &__img {
  	display: block;
  }

  &__left {
  	margin-right: 6px;
  }

  &__categ {
  	display: inline-block;
  	width: auto;
  	padding: 3px 8px;
  	font-size: 12px;
  	line-height: 14px;
  	text-transform: uppercase;
  	border-radius: 3px;

  	&--magazine {
  		background-color: #54d154;
  	}

  	&--ui {
  		background-color: #ffba00;
  	}

  	&--photography {
  		background-color: #089dea;
  	}

  	&--book {
  		background-color: #f34d04;
  	}
  }
	
	&__form {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 322px;
	}

	&__label {
		display: block;
		margin-bottom: 2px;
		padding: 8px 15px;
		background-color: #444;

		&:first-child {
			border-radius: 3px 3px 0 0;
		}
	}

	&__input {
		width: 100%;
		color:#fff;
		font-size: 16px;
		line-height: 18px;
	}

	&__textarea {
		min-height: 130px;
		margin-bottom: 2px;
		padding: 8px 15px;
		font-family: $font-family-1;
		color:#fff;
		font-size: 16px;
		line-height: 22px;
		background-color: #444;
		border: none;
	}

	textarea::placeholder {
	  color: #777;  
	}

	&__btn {
		color: #fff;
		font-size: 14px;
		border-radius: 0 0 5px 5px;

		&:hover {
			color: $link-color--hover;
	    letter-spacing: .1em;
	    background-color: #fff;
	  }
	}
}
